body {
  background: #fff;
  height: 100%;
  font-size: 16px;
  font-family: "proximanova-regular", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  min-width: 970px;
  margin: 0 auto;
}

h1, h2, h3, h4, h5, h6, p, strong, em {
  font-weight: normal;
  font-style: normal;
}

.title-lg {
  font-size: 48px;
}

.title-xl {
  font-size: 30px;
  line-height: 1;
  color: #000;
  font-family: "proximanova-regular", Helvetica Neue, Helvetica, Arial, sans-serif;
}

.posts {
  text-align: left;
  font-size: 14px;
  display: inline-block;
  vertical-align: top;
  width: 100%;
  padding-top: 15px;
}

.posts:after {
  content: " ";
  display: table;
  clear: both;
}

.posts__title {
  color: #7c8695;
  display: inline-block;
  margin-bottom: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 75%;
}

.posts__link {
  float: right;
  text-decoration: none;
  color: #1c2534;
  transition: color .24s ease-in-out;
}

.posts__link:hover {
  text-decoration: underline;
  color: black;
}

.posts .post {
  font-size: 0;
  margin-bottom: 20px;
}

.posts .post:after {
  content: " ";
  display: table;
  clear: both;
}

.posts .post > .post__name {
  margin-bottom: 5px;
}

.posts .post__desc {
  display: table-cell;
  line-height: 1.33;
  max-width: 125px;
}

.posts .post__desc-link {
  font-size: 12px;
  color: #1c2534;
  text-decoration: none;
  display: inline-block;
}

.posts .post__img {
  width: 80px;
  height: 80px;
  display: inline-block;
  vertical-align: top;
  border-radius: 3px;
  float: left;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  margin-right: 10px;
}

.posts .post__name {
  color: #1c2534;
  display: block;
  font-size: 16px;
}

.posts .post__text {
  color: #7c8695;
  font-size: 14px;
  display: block;
  text-decoration: none;
}

.posts .post__text:hover {
  text-decoration: underline;
}

.footer {
  font-size: 12px;
  color: #b7bfcd;
  text-align: left;
  margin-top: 15px;
}

.footer p {
  margin-bottom: 17px;
}

.footer__link {
  display: inline-block;
  vertical-align: top;
  color: inherit;
  margin-right: 20px;
  transition: color .24s ease-in-out;
  text-decoration: none;
}

.footer__link:hover {
  text-decoration: underline;
  color: #71973c;
}

.inner {
  max-width: 1440px;
  padding: 0 15px;
  margin: 0 auto;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1439px) {
  .inner {
    max-width: 970px;
  }
}

.main {
  text-align: center;
}

.main .content {
  padding: 0 15px;
  width: auto;
  margin: 0 215px;
}

.main .content:after {
  content: " ";
  display: table;
  clear: both;
}

.sidebar {
  width: 100%;
  max-width: 215px;
  display: inline-block;
  vertical-align: top;
}

.sidebar.sidebar_left {
  float: left;
}

.sidebar.sidebar_left .posts {
  border-top: 1px solid #e0e2e6;
  border-bottom: 1px solid #e0e2e6;
}

.sidebar.sidebar_left .banner {
  margin-bottom: 20px;
}

.sidebar.sidebar_right {
  float: right;
}

.sidebar.sidebar_right .posts {
  border-bottom: 1px solid #e0e2e6;
}

.sidebar.sidebar_right .banner__img {
  border: 1px solid #e0e2e6;
}

.features {
  padding-top: 10px;
  margin-bottom: 20px;
  text-align: left;
}

.features__btn {
  float: right;
}

.features__title {
  color: #1c2534;
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 65%;
}

.banner {
  text-decoration: none;
  text-align: left;
  line-height: 1;
  display: block;
  margin: 0 auto;
  max-width: 210px;
  width: 100%;
  font-size: 0;
}

.banner__img {
  display: block;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  height: 300px;
  width: 100%;
}

.banner__title {
  font-size: 14px;
  margin: 20px auto 17px;
  color: #7c8695;
  display: inline-block;
  vertical-align: top;
}

.banner__caption {
  font-size: 11px;
  color: #d3d3d3;
  margin-top: 8px;
  line-height: 1;
  display: block;
}

.banner__caption a {
  color: inherit;
  text-decoration: none;
  transition: color .24s ease-in-out;
}

.banner__caption a:hover {
  text-decoration: underline;
  color: #71973c;
}

body.preview-wrapper .header {
  padding-top: 40px;
  border: none;
  background-image: url("../img/bg-preview.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

body.preview-wrapper .header__overlay {
  position: relative;
}

body.preview-wrapper .header__overlay:before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  z-index: 0;
  background-color: rgba(28, 37, 52, 0.9);
}

body.preview-wrapper .header__in {
  max-width: 1380px;
  padding: 0 20px;
  position: relative;
  z-index: 5;
}

body.preview-wrapper .header__in:after {
  content: " ";
  display: table;
  clear: both;
}

body.preview-wrapper .header__nav {
  display: inline-block;
  width: 100%;
  vertical-align: top;
}

body.preview-wrapper .header__nav:after {
  content: " ";
  display: table;
  clear: both;
}

body.preview-wrapper .header__nav .logo {
  float: left;
}

body.preview-wrapper .header__nav .btn_login {
  float: right;
  margin-top: 5px;
}

body.preview-wrapper .header__desc {
  padding: 100px 0 195px;
  text-align: center;
  max-width: 490px;
  margin: 0 auto;
  font-family: "proximanova-light", Helvetica Neue, Helvetica, Arial, sans-serif;
}

body.preview-wrapper .header__desc .title-lg {
  color: #f3f4f6;
}

body.preview-wrapper .header__desc p {
  font-size: 20px;
  color: #f3f4f6;
  line-height: 1.2;
  margin-top: 15px;
}

.header {
  background-color: #87b448;
  padding: 11px 0 15px 0;
  font-size: 0;
  border-bottom: 2px solid #71973c;
}

.header__bar {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
  padding: 0 20px;
}

.header__bar-link {
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  padding: 0 10px;
  display: inline-block;
  font-size: 24px;
  fill: #fff;
}

.header__bar-link svg {
  font-size: inherit;
  display: inline-block;
  vertical-align: middle;
}

.header__user {
  display: inline-block;
  vertical-align: middle;
}

.header__user-ava {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  overflow: hidden;
  border-radius: 3px;
  margin-right: 11px;
  margin-left: 3px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  width: 32px;
  height: 32px;
}

.header__user-ava img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header__user-name {
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  line-height: 2.265;
  color: #fefefe;
  text-decoration: none;
  transition: color .24s ease-in-out;
}

.header__user-name:hover {
  color: #fff;
}

.search {
  display: inline-block;
  max-width: 435px;
  vertical-align: middle;
  width: 100%;
  position: relative;
  font-size: 0;
  margin-left: 30px;
}

.search__input {
  border-radius: 3px;
  background-color: transparent;
  outline: none;
  padding: 9px 9px 9px 38px;
  background-color: #73993d;
  color: #fff;
  width: 100%;
  font-size: 15px;
}

.search__input::placeholder {
  color: #fff;
}

.search__input:focus {
  color: #fff;
}

.search__input:focus + .search__icon {
  opacity: .5;
}

.search__icon {
  position: absolute;
  top: 9px;
  left: 10px;
  display: block;
  width: 16px;
  height: 16px;
  transition: opacity .24s ease-in-out;
  fill: #87b448;
}

.logo {
  float: left;
  font-size: 0;
  width: 130px;
  height: 40px;
  display: inline-block;
  background-image: url(../img/logo.png);
}

@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
  .logo {
    background-image: url(../img/logo@2x.png);
  }
}

.menu {
  font-size: 0;
  text-align: left;
}

.menu__item {
  display: block;
}

.menu__link {
  text-decoration: none;
  font-family: "proximanova-regular", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 16px;
  color: #59606b;
  padding: 15px 0 15px 50px;
  display: block;
  line-height: 1.2;
  position: relative;
  transition: color .24s ease-in-out;
  border-bottom: 1px solid #e0e2e6;
}

.menu__link:hover {
  color: #2b2e33;
}

.menu__link:hover .menu__icon svg {
  fill: #8a97b0;
}

.menu__icon {
  position: absolute;
  top: 12px;
  left: 4px;
}

.menu__icon svg {
  transition: fill .24s ease-in-out;
  fill: #c7cdd9;
  font-size: 24px;
  display: inline-block;
  vertical-align: middle;
}

.btn {
  cursor: pointer;
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  padding: 8px 20px;
  line-height: 1;
  background-color: rgba(0, 0, 0, 0.61);
  transition: background-color .24s ease-in-out, color .24s ease-in-out;
  position: relative;
}

.btn:active {
  top: 1px;
}

.btn_green {
  background-color: #87b448;
}

.btn_rounded {
  border-radius: 4px;
}

input.error,
input.valid,
select.error,
select.valid {
  background-position: right 24px center;
}

input.error {
  border-color: red !important;
}

.textarea {
  width: 100%;
  resize: none;
  background-color: #fff;
  font-size: 16px;
  border: 1px solid #c8cdd5;
  font-family: "proximanova-regular", Helvetica Neue, Helvetica, Arial, sans-serif;
  border-radius: 4px;
  padding: 20px;
}

.textarea::placeholder {
