}

.input-field {
  display: inline-block;
  vertical-align: top;
  width: 100%;
}

.input {
  display: inline-block;
  vertical-align: top;
  margin: 0;
  font-size: 16px;
  width: 100%;
  border: 1px solid #c8cdd5;
  padding: 20px 20px 19px 19px;
  border-radius: 4px;
}

.input::placeholder {
