.form {
  background-color: #fff;
  border-radius: 5px;
  position: relative;
  padding: 19px;
  top: -100px;
  max-width: 760px;
  margin: 0 auto;
  border: 1px solid #e0e2e6;
  z-index: 10;
}

.form:after {
  content: " ";
  display: table;
  clear: both;
}

.form__title {
  text-align: left;
  color: #c1c1c1;
  font-size: 16px;
  margin-bottom: 7px;
}

.form__row {
  display: inline-block;
  width: 100%;
  font-size: 0;
}

.form__row:after {
  content: " ";
  display: table;
  clear: both;
}

.form__row > .form__field {
  display: inline-block;
  width: 100%;
  max-width: 345px;
  float: left;
  margin-right: 30px;
}

.form__row > .form__field:last-child {
  margin-right: 0;
}

.form__field {
  vertical-align: top;
  text-align: left;
  margin-bottom: 20px;
}

.form__field:after {
  content: " ";
